        <p>
          由于企鹅项目上线时受到未知黑客攻击，导致开盘30分钟还不能正常质押，官方紧急修复，但资金已被盗走，为保证广大用户的权益，团队成员经过10多天不懈的努力，已经恢复部分数据，但更多数据还在找回当中。丢失的部分资金将由项目方承担，同时，我们将于数据找回后启动清退工作，对于恶意攻击机构或者个人，我们将追究其法律责任。同时，我们由衷的对参与用户和广大鹅友致以最真诚的歉意，后续项目将对参与用户地址进行一些空投代币作为补偿，具体清退规则将于清退时发币。
        </p>
      </div>
      <div class="rules">
        <h2>清退登记</h2>
        <div class="content">
          <p>
            清退登记时间为2020年10月10日18点-2020年20月25日12点，在这期间未登记的用户无法清退。登记时请按照当时质押的交易对和数量如实填写，若信息有误概不清退。
          </p>

          <div class="regisration">
            <div class="input">
              <span>质押地址: </span>
              <input
                type="text"
                placeholder="请输入质押时的地址"
                v-model="address"
                class="w3-input"
              />
            </div>
            <div class="input">
              <span>质押交易对: </span>
              <select
                name="lpPair"
                id="lpPair"
                v-model="lpPair"
                class="w3-input"
              >
                <option value="USDT/TRX">USDT/TRX</option>
                <option value="SUN/TRX">SUN/TRX</option>
                <option value="USDJ/TRX">USDJ/TRX</option>
                <option value="JST/TRX">JST/TRX</option>
                <option value="JFI/TRX">JFI/TRX</option>
              </select>
            </div>
            <div class="input">
              <span>质押数量：</span>
              <input
                type="text"
                placeholder="0"
                v-model="amount"
                class="w3-input"
              />
            </div>
            <div class="error" v-if="errorInfo">
              <span>{{ errorInfo }}</span>
            </div>
            <!-- <button class="w3-btn w3-green btn">登记</button> -->
            <VueLoadingButton
              :loading="loadingResult"
              class="w3-btn w3-green btn"
              @click.native="register()"
            >
              登记
            </VueLoadingButton>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import VueLoadingButton from "vue-loading-button";

export default {
  name: "Home",
  components: {
    VueLoadingButton,
  },
  data() {
    return {
      address: "",
      lpPair: "",
      amount: "",
      registerPoolAddress: "TBmYwMLqrjWFJqzKPRWUKYxUx3K93bismx",
      registerPoolContract: null,
      errorInfo: "",
      loadingResult: false,
    };
  },
  methods: {
    async ensureTronWebLoaded() {
      while (!window.tronWeb || !window.tronWeb.defaultAddress.base58) {
        await new Promise((r) => setTimeout(r, 100));
      }
    },
    async getTronWeb() {
      await this.ensureTronWebLoaded();
      return window.tronWeb;
    },
    async getPoolContract() {
      const tronWeb = await this.getTronWeb();
      if (!this.registerPoolContract) {
        this.registerPoolContract = await tronWeb
          .contract()
          .at(this.registerPoolAddress);
      }
      return this.registerPoolContract;
    },
    parseErrorMessage(message) {
      console.log(message);
      if (message.includes("not start")) {
        return "尚未开始清退登记";
      } else if (message.includes("already finished")) {
        return "清退登记已结束";
      } else {
        return message;
      }
    },
    async parseTransactionResult(transaction) {
      const tronWeb = await this.getTronWeb();
      // console.log(transaction)

      let result = {};
      while (!("ret" in result)) {
        await new Promise((r) => setTimeout(r, 1000));
        try {
          result = await tronWeb.trx.getTransaction(transaction);
        } catch (e) {
          if (e === "Transaction not found") {
            continue;
          } else {
            this.errorInfo = e;
            return false;
          }
        }
      }

      if (result.ret[0].contractRet != "REVERT") {
        return true;
      }
      this.errorInfo = "清退登记失败，正在获取错误信息";

      result = await tronWeb.trx.getTransactionInfo(transaction);
      while (!("blockNumber" in result)) {
        await new Promise((r) => setTimeout(r, 1000));
        result = await tronWeb.trx.getTransactionInfo(transaction);
      }

      // console.log(result);
      if (result.result === "FAILED") {
        const message = result.contractResult[0];
        this.errorInfo = this.parseErrorMessage(tronWeb.toUtf8(message));
        return false;
      }
      return true;
    },
    async register() {
      this.loadingResult = true;
      const tronWeb = await this.getTronWeb()
      const poolContract = await this.getPoolContract();
      const transaction = await poolContract
        .register(this.address, this.lpPair, tronWeb.toDecimal(this.amount) * 1000000)
        .send();
      console.log(transaction);
      
      const result = await this.parseTransactionResult(transaction);
      this.loadingResult = false;
      if (result) {
        this.errorInfo = '登记成功'
      }
    },
  },
};
</script>

